<template>
  <div class="plist" ref="plist">
    <div v-if="folderName">
      <div class="fast-move">
        <q-btn
          flat
          round
          color="primary"
          icon="expand_less"
          @click="goUp"
          :class="visibleClassTop"
        />
        <q-btn
          flat
          round
          color="primary"
          icon="expand_more"
          @click="goDown"
          :class="visibleClassBottom"
        />
      </div>
      <div class="top-marker marker" v-intersection="onIntersectionTop"></div>
      <div class="header">
        <q-btn
          class="head-btn"
          :color="btnColorCreatePlist"
          @click="createPlist"
          label="Создать"
          :icon-right="btnIconCreatePlist"
          :loading="isCreatePlist"
          :disable="isCreatePlist"
        >
          <template v-slot:loading>
            <q-spinner-tail size="sm" />
          </template>
        </q-btn>
        <q-btn
          class="head-btn"
          :color="btnColorSavePlist"
          @click="savePlist"
          label="Сохранить"
          :icon-right="btnIconSavePlist"
          :loading="isSavePlist"
          :disable="isSavePlist"
        >
          <template v-slot:loading>
            <q-spinner-tail size="sm" />
          </template>
        </q-btn>
      </div>
      <div v-if="isLoading">
        <q-spinner-tail class="spinner-load" color="primary" size="2em" />
      </div>
      <div v-else>
        <div class="edit">
          <q-card class="item edit-categories" :class="{'pin-category': pin}">
            <Category />
          </q-card>
          <q-card class="item edit-pages">
            <Pages />
          </q-card>
          <q-card class="item edit-version">
            <Version />
          </q-card>
        </div>
      </div>

      <div class="bottom-marker marker" v-intersection="onIntersectionBottom"></div>
    </div>
    <div v-else>
      <h6 class="no-file">Файл не выбран</h6>
    </div>
  </div>
</template>

 <script>
import { mapState } from 'vuex';
import { saveBtn } from '@/mixins/saving';

import Category from '@/components/plist/Category.vue';
import Pages from '@/components/plist/Pages.vue';
import Version from '@/components/plist/Version.vue';

export default {
  name: 'Plist',
  components: {
    Category,
    Pages,
    Version,
  },
  mixins: [saveBtn],
  data: function () {
    return {
      isLoading: true,
      visibleTop: false,
      visibleBottom: false,
      btnColorCreatePlist: 'primary',
      btnIconCreatePlist: 'create',
      isCreatePlist: false,
      btnColorSavePlist: 'primary',
      btnIconSavePlist: 'save',
      isSavePlist: false,
    };
  },
  computed: {
    ...mapState({
      baseURL: (state) => state.baseURL,
      folderName: (state) => state.folderName,
      plist: (state) => state.plist.plist,
      globalOptions: (state) => state.settings.options,
    }),
    pin: {
      get() {
        return this.$store.state.plist.pinCategory;
      },
      set(value) {
        this.$store.commit('setPinCategory', value);
      },
    },
    redirectAfterFiles: {
      get() {
        return this.$store.state.redirectAfterFiles;
      },
      set(value) {
        this.$store.commit('setRedirectAfterFiles', value);
      },
    },
    visibleClassTop() {
      return `fast-move-${this.visibleTop ? 'positive' : 'negative'}`;
    },
    visibleClassBottom() {
      return `fast-move-${this.visibleBottom ? 'positive' : 'negative'}`;
    },
  },
  watch: {},
  methods: {
    scrollTo(align) {
      const plist = this.$refs.plist;
      plist.scrollIntoView({ block: align, behavior: 'smooth' });
    },
    goUp() {
      this.scrollTo('start');
    },
    goDown() {
      this.scrollTo('end');
    },
    onIntersectionTop(entry) {
      this.visibleTop = entry.isIntersecting;
    },
    onIntersectionBottom(entry) {
      this.visibleBottom = entry.isIntersecting;
    },
    async createPlist() {
      this.isCreatePlist = true;

      let response, result;
      try {
        response = await fetch(this.baseURL + 'create_plist', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ folderName: this.folderName }),
        });
        result = await response.json();

        if (result.error && result.error !== '') {
          this.saveAction('btnIconCreatePlist', 'btnColorCreatePlist', false); //mixin
          console.error(result.error);
        } else {
          this.saveAction('btnIconCreatePlist', 'btnColorCreatePlist'); //mixin
        }
      } catch (error) {
        this.saveAction('btnIconCreatePlist', 'btnColorCreatePlist', false); //mixin
        console.error(error);
      }

      this.addID(result);
      this.$store.commit('setPlist', result);

      this.isCreatePlist = false;
    },
    async savePlist() {
      this.isSavePlist = true;

      this.removeID(this.plist);

      let response, result;
      try {
        response = await fetch(this.baseURL + 'save_plist', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({
            folderName: this.folderName,
            plist: this.plist,
          }),
        });
        result = await response.json();

        if (result.error && result.error !== '') {
          this.saveAction('btnIconSavePlist', 'btnColorSavePlist', false); //mixin
          console.error(result.error);
        } else {
          this.saveAction('btnIconSavePlist', 'btnColorSavePlist'); //mixin
        }
      } catch (error) {
        this.saveAction('btnIconSavePlist', 'btnColorSavePlist', false); //mixin
        console.error(error);
      }
      this.addID(result);
      this.$store.commit('setPlist', result);

      this.isSavePlist = false;
    },
    async getPlist() {
      let response, result;
      try {
        response = await fetch(this.baseURL + 'plist', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ folderName: this.folderName }),
        });
        result = await response.json();
      } catch (error) {
        console.error(error);
      }

      if (result.error && result.error !== '') {
        console.error(result.error);
      }

      this.addID(result);
      this.$store.commit('setPlist', result);
      this.isLoading = false;
    },
    addID({ categories, pages }) {
      function createID(arr) {
        for (let i = 0; i < arr.length; i++) {
          arr[i].id = String(i);
        }
      }

      createID(categories);
      createID(pages);
    },
    removeID({ categories, pages }) {
      function createID(arr) {
        for (let i = 0; i < arr.length; i++) {
          delete arr[i].id;
        }
      }

      createID(categories);
      createID(pages);
    },
    getGlobalOptions() {
      
    },
  },
  mounted() {
    this.redirectAfterFiles = '/plist';
  },
  created() {
    this.getPlist();
    this.getGlobalOptions();
  },
};
</script>

 <style  lang="scss" scoped>
.header {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding: 1rem 0;
  background: #fff;
  z-index: 2;
}

.header .left .head-btn {
  margin-right: 1rem;
}

.header .right .head-btn {
  margin-left: 1rem;
}

.edit .item {
  margin-bottom: 2rem;
}

.fast-move {
  position: fixed;
  bottom: 1rem;
  right: 0rem;
  display: flex;
  flex-direction: column;
  z-index: 1;
}

.marker {
  width: 100%;
  height: 1px;
}

.fast-move-positive {
  opacity: 0;
  cursor: none;
  pointer-events: none;
}

.edit-categories {
}

.pin-category {
  position: sticky;
  top: 5rem;
  z-index: 10;
}

.pin-category:before {
  position: absolute;
  top: -1rem;
  left: 0;
  width: 100%;
  height: 1rem;
  content: '';
  background: #fff;
  z-index: 9;
}
</style>