<template>
  <div class="category">
    <q-card-section>
      <div class="top">
        <h5 class="title">Категории</h5>
        <q-toggle v-model="pin" color="primary" icon="push_pin" label="Закрепить" />
      </div>

      <div v-if="pin">
        <div class="min" v-for="(category, index) of plist.categories" :key="index">
          <span class="name">{{ index }}</span>
          &nbsp;
          <span>{{ category.name }}</span>
        </div>
      </div>
      <div v-else>
        <draggable
          v-model="plist.categories"
          group="pages"
          v-bind="dragOptions"
          @start="drag=true"
          @end="drag=false"
          handle=".categories-handle"
        >
          <transition-group type="transition" :name="!drag ? 'flip-list' : null">
            <q-card
              class="content"
              v-for="(category, index) of plist.categories"
              :key="category.id"
            >
              <q-card-section class="content-section">
                <div>
                  <div class="info">
                    <h6>{{ index }}</h6>
                    <q-input dense class="name" v-model.lazy="category.name" label="Название" />
                  </div>

                  <div class="inner-color">
                    <div class="color">
                      <q-input dense v-model="category.textColor" label="Цвет текста" />
                      <div class="preview" :style="{background: category.textColor}"></div>
                    </div>
                    <div class="color">
                      <q-input dense v-model="category.backgroundColor" label="Цвет фона" />
                      <div class="preview" :style="{background: category.backgroundColor}"></div>
                    </div>
                    <div class="color">
                      <q-input
                        dense
                        v-model="category.selectedTextColor"
                        label="Выделенный цвет текста"
                      />
                      <div class="preview" :style="{background: category.selectedTextColor}"></div>
                    </div>
                    <div class="color">
                      <q-input
                        dense
                        v-model="category.selectedBackgroundColor"
                        label="Выделенный цвет фона"
                      />
                      <div class="preview" :style="{background: category.selectedBackgroundColor}"></div>
                    </div>
                  </div>
                </div>
                <div class="options">
                  <div class="wrap">
                    <q-btn
                      flat
                      round
                      class="options-item"
                      color="negative"
                      icon="delete"
                      title="Удалить"
                      @click="removeCategory(index)"
                    />
                    <q-btn
                      flat
                      round
                      class="options-item"
                      color="primary"
                      icon="content_copy"
                      title="Копировать"
                      @click="addCategory(category, index)"
                    />
                    <q-btn
                      flat
                      round
                      class="options-item"
                      color="primary"
                      icon="add"
                      title="Создать"
                      @click="addCategory({}, index)"
                    />
                  </div>
                  <div class="wrap">
                    <q-btn
                      flat
                      round
                      class="drag-handle categories-handle options-item"
                      color="primary"
                      icon="drag_handle"
                      title="Переместить"
                    />
                  </div>
                </div>
              </q-card-section>
            </q-card>
          </transition-group>
        </draggable>

        <q-btn
          color="primary"
          class="full-width"
          @click="addCategory()"
          label="Добавить категорию"
        />
      </div>
    </q-card-section>
  </div>
</template>

 <script>
import draggable from 'vuedraggable';
import { mapState } from 'vuex';

class Category {
  constructor(
    id,
    {
      name = '',
      textColor = '#ffffff',
      backgroundColor = '#ffffff',
      selectedTextColor = '#ffffff',
      selectedBackgroundColor = '#ffffff',
    }
  ) {
    this.id = id;
    this.name = name;
    this.textColor = textColor;
    this.backgroundColor = backgroundColor;
    this.selectedTextColor = selectedTextColor;
    this.selectedBackgroundColor = selectedBackgroundColor;
  }
}

export default {
  name: 'Category',
  components: { draggable },
  data: function () {
    return {
      drag: false,
      maxID: '',
    };
  },
  filters: {
    color(value) {
      if (!value) return '';
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
  },
  computed: {
    ...mapState({
      plist: (state) => state.plist.plist,
    }),
    pin: {
      get() {
        return this.$store.state.plist.pinCategory;
      },
      set(value) {
        this.$store.commit('setPinCategory', value);
      },
    },
    dragOptions() {
      return {
        animation: 200,
        group: 'pages',
        disabled: false,
        ghostClass: 'ghost',
      };
    },
  },
  watch: {
    'plist.categories': {
      handler: function (val, oldVal) {
        // Проверка для цвета, есть ли символ '#' в начале строки, если нет то добавить
        for (const iterator of val) {
          iterator.textColor = checkColor(iterator.textColor);
          iterator.selectedTextColor = checkColor(iterator.selectedTextColor);
          iterator.backgroundColor = checkColor(iterator.backgroundColor);
          iterator.selectedBackgroundColor = checkColor(
            iterator.selectedBackgroundColor
          );
        }

        function checkColor(string) {
          if (string.charAt(0) !== '#') {
            return '#' + string;
          } else {
            return string;
          }
        }
      },
      deep: true,
    },
  },
  methods: {
    removeCategory(index) {
      this.plist.categories.splice(index, 1);
    },
    addCategory(payload = {}, index) {
      ++this.maxID;
      if (typeof index === 'number') {
        this.plist.categories.splice(
          ++index,
          0,
          new Category(this.maxID, payload)
        );
      } else {
        this.plist.categories.push(new Category(this.maxID, payload));
      }
    },
  },
  mounted() {
    this.maxID = this.plist.categories[this.plist.categories.length - 1].id;
  },
  created() {},
};
</script>

 <style  lang="scss" scoped>
.category .top {
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.category .title {
  margin: 0;
}

.category .info {
  display: grid;
  grid-template-columns: 2rem 1fr;
  align-items: center;
  grid-gap: 1rem;
}

.category .info h6 {
  margin: 0;
}

.category .inner-color {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1rem;
}

.category .content {
  margin-bottom: 1rem;
}

.category .name {
  margin-bottom: 1rem;
}

.category .color {
  position: relative;
}

.category .color .preview {
  position: absolute;
  top: 50%;
  right: 0;
  width: 1rem;
  height: 1rem;
  transform: translateY(-50%);
  z-index: 1;
  border: 1px solid rgb(160, 160, 160);
}

.content-section {
  display: grid;
  grid-template-columns: 1fr 5rem;
  grid-column-gap: 1rem;
}

.options {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.options .options-item:not(:last-child) {
  margin-bottom: 1rem;
}

.min {
  display: flex;
	border-bottom: 1px solid rgb(202, 202, 202);
}

.min .name {
  font-weight: 600;
  padding-right: 1rem;
  margin-bottom: 0;
}

/* drag */
.drag-handle {
  cursor: move;
}

.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
/* drag END */
</style>