<template>
  <div class="pages">
    <q-dialog v-model="isInfoDialog">
      <q-card>
        <q-card-section>
          <div class="text-h6">Info "Страницы"</div>
        </q-card-section>

        <q-card-section class="q-pt-none">
          Добавление категории для страниц группой:
          <ul>
            <li>Поставить курсор в ячейку с категорией (в ней должно быть значение, которое скопируется в другие ячейки)</li>
            <li>Зажать SHIFT</li>
            <li>Поставить курсор в ячейку до которой нужно копировать</li>
          </ul>
        </q-card-section>
      </q-card>
    </q-dialog>

    <q-card-section>
      <div class="top">
        <h5 class="title">Страницы</h5>
        <q-btn flat round color="primary" icon="info" @click="isInfoDialog = true" />
      </div>

      <draggable
        v-model="plist.pages"
        group="pages"
        v-bind="dragOptions"
        @start="drag=true"
        @end="drag=false"
        handle=".pages-handle"
      >
        <transition-group type="transition" :name="!drag ? 'flip-list' : null">
          <q-card class="content" v-for="(page, index) of plist.pages" :key="page.id">
            <q-card-section class="content-section">
              <div class="edit">
                <q-input dense v-model="page.archive" />
                <q-input
                  dense
                  v-model="page.categorie"
                  @focus="focusCopyCategory(index, page.categorie)"
                  @input="copyCategory.val = page.categorie"
                />
              </div>

              <div class="options">
                <q-btn
                  flat
                  round
                  class="options-item"
                  color="negative"
                  icon="delete"
                  title="Удалить"
                  @click="removePage(index)"
                />
                <q-btn
                  flat
                  round
                  class="options-item"
                  color="primary"
                  icon="content_copy"
                  title="Копировать"
                  @click="addPage(page, index)"
                />
                <q-btn
                  flat
                  round
                  class="options-item"
                  color="primary"
                  icon="add"
                  title="Создать"
                  @click="addPage({}, index)"
                />
                <q-btn
                  flat
                  round
                  class="drag-handle pages-handle options-item"
                  color="primary"
                  icon="drag_handle"
                  title="Переместить"
                />
              </div>
            </q-card-section>
          </q-card>
        </transition-group>
      </draggable>

      <q-btn color="primary" class="full-width" @click="addPage()" label="Добавить страницу" />
    </q-card-section>
  </div>
</template>

 <script>
import draggable from 'vuedraggable';

import { mapState } from 'vuex';

class Page {
  constructor(id, { archive = '', categorie = '' }) {
    this.id = id;
    this.archive = archive;
    this.categorie = categorie;
  }
}

export default {
  name: 'Pages',
  components: { draggable },
  data: function() {
    return {
      isInfoDialog: false,
      drag: false,
      copyCategory: {
        index: 0,
        val: 0
      },
			isShiftKey: false,
			maxID: '',
    };
  },
  computed: {
    ...mapState({
      plist: state => state.plist.plist
    }),
    dragOptions() {
      return {
        animation: 200,
        group: 'pages',
        disabled: false,
        ghostClass: 'ghost'
      };
    }
  },
  watch: {},
  methods: {
    focusCopyCategory(index, val) {
      // Копирование значений из категории
      if (this.isShiftKey) {
        let copyIndex = this.copyCategory.index;

        if (copyIndex > index) {
          // Копировать снизу вверх
          for (let i = index; i <= copyIndex; i++) {
            this.plist.pages[i].categorie = this.copyCategory.val;
          }
        } else {
          // Копировать  сверху вниз
          for (let i = copyIndex; i <= index; i++) {
            this.plist.pages[i].categorie = this.copyCategory.val;
          }
        }
      } else {
        this.copyCategory.index = index;
        this.copyCategory.val = val;
      }
    },
    removePage(index) {
      this.plist.pages.splice(index, 1);
    },
    addPage(payload = {}, index) {
			++this.maxID
      if (typeof index === 'number') {
        this.plist.pages.splice(++index, 0, new Page(this.maxID, payload));
      } else {
        this.plist.pages.push(new Page(this.maxID, payload));
      }
    },
    keyUp(event) {
      if (event.keyCode === 16) {
        this.isShiftKey = false;
      }
    },
    keyDown(event) {
      if (event.shiftKey) {
        this.isShiftKey = true;
      }
    },
    keyDestroy() {
      document.removeEventListener('keydown', this.keyDown);
      document.removeEventListener('keyup', this.keyUp);
    }
  },
  mounted() {
		this.maxID =	this.plist.pages[this.plist.pages.length - 1].id
    document.addEventListener('keydown', this.keyDown);
    document.addEventListener('keyup', this.keyUp);
  },
  created() {},
  beforeDestroy() {
    this.keyDestroy();
  }
};
</script>

 <style  lang="scss" scoped>
.pages .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.pages .title {
  margin: 0;
}

.pages .content {
  margin-bottom: 1rem;
}

.pages .content-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pages .content-section .edit {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3rem;
  grid-column-gap: 1rem;
  margin-right: 2rem;
}

.pages .options {
  display: flex;
  align-items: center;
}

.pages .options .options-item:not(:last-child) {
  margin-right: 0.5rem;
}

/* drag */
.drag-handle {
  cursor: move;
}

.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
/* drag END */
</style>