<template>
  <div class="version">
    <q-card-section>
      <h5 class="title">Версия</h5>
      <div class="wrapper">
        <q-input dense class="name" v-model="plist.version" label="Версия" />
        <q-btn color="primary" @click="increaseVersion" label="Увеличить" />
      </div>
    </q-card-section>
  </div>
</template>

 <script>
import { mapState } from 'vuex';

export default {
  name: 'Version',
  data: function() {
    return {};
  },
  computed: {
    ...mapState({
      plist: state => state.plist.plist
    })
  },
  watch: {},
  methods: {
		increaseVersion(){
			this.plist.version = (+this.plist.version + 0.1).toFixed(1);
		}
	},
  mounted() {},
  created() {}
};
</script>

 <style  lang="scss" scoped>
.version .title {
  margin: 0;
  margin-bottom: 1rem;
}

.version .wrapper {
  display: grid;
  grid-template-columns: 3rem 1fr;
  align-items: center;
  grid-column-gap: 1rem;
}
</style>